@charset "UTF-8";
:root {
  --color-red-norml: rgb(191, 39, 56);
  --color-red-hover: rgb(204, 42, 60);
  --color-red-active: rgb(153, 31, 45);
  --color-red-disabled: rgb(234, 234, 234);
  --color--grey-normal: rgb(245, 245, 245);
  --color-red-error-bg: rgba(191, 39, 56, 0.12);
  --color-black-normal: rgb(38, 38, 51);
  --color-white-normal: rgb(255, 255, 255);
  --color-grey-light: rgb(121, 121, 134);
  --color-grey-superlight: rgb(173, 173, 175);
  --color-grey-dark: rgb(42, 42, 46);
  --color-black-dark: rgb(34, 34, 37);
  --padding: 127px;
  --resetcolor: rgb(255, 255, 255);
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Golos Text", sans-serif;
}

.container {
  max-width: 1322px;
  height: max-content;
  padding: 0 16px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

ul,
ol {
  list-style-type: none;
}

#map {
  width: 100%;
  height: 599px;
  transition: 0.5s all;
}
@media (max-width: 768px) {
  #map {
    height: 480px;
  }
}
@media (max-width: 320px) {
  #map {
    height: 477px;
  }
}

.target {
  width: 36px;
  height: 40px;
  background: url("icons/target.svg") no-repeat center/cover;
}

header {
  width: 100%;
  max-height: 92px;
  background-color: var(--color-white-normal);
}

.header {
  width: 100%;
  height: 92px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 0 15px 0;
  position: relative;
}
@media (max-width: 768px) {
  .header {
    height: 65px;
  }
}
.header__logo {
  width: 106px;
  height: 64px;
  background: url("icons/logo_desctop.svg") no-repeat center/cover;
  position: sticky;
  left: 50%;
  transform: translateX(-50%);
}
@media (min-width: 321px) and (max-width: 768px) {
  .header__logo {
    width: 90px;
    height: 30px;
    background: url("icons/logo_tablet.svg") no-repeat center/cover;
  }
}
@media (max-width: 320px) {
  .header__logo {
    width: 67px;
    height: 40px;
  }
}
.header__burger {
  width: clamp(23px, calc(34 / 12.9)vw, 34px);
  height: 12px;
  cursor: pointer;
}
.header__burger span {
  display: block;
  width: 100%;
  height: 2px;
  border-radius: 5px;
  background-color: var(--color-black-normal);
}
.header__burger span:nth-child(2) {
  width: 50%;
  margin-top: 10px;
}
.header__menulist {
  width: 408px;
}
@media (max-width: 1040px) {
  .header__menulist {
    width: 317px;
  }
}
@media (max-width: 928px) {
  .header__menulist {
    width: 200px;
  }
}
@media (max-width: 620px) {
  .header__menulist {
    display: none;
  }
}
.header__menulist_list {
  width: 100%;
  display: flex;
  gap: 46px;
}
@media (max-width: 925px) {
  .header__menulist_list {
    gap: 30px;
  }
}
.header__menulist_list-link {
  color: var(--color-black-normal);
  font-size: 15px;
  font-weight: 400;
  line-height: 18px;
}
@media (max-width: 768px) {
  .header__menulist_list-link {
    font-size: 14px;
  }
}
@media (max-width: 1070px) {
  .header__menulist_list-item:nth-child(3) {
    display: none;
  }
}
.header__icons-wrap {
  display: flex;
  gap: 22px;
}
.header__icons-wrap_graf {
  cursor: pointer;
  display: block;
  width: 12px;
  height: 18px;
}
.header__icons-wrap_search {
  cursor: pointer;
  display: block;
  width: 18px;
  height: 19px;
}
@media (max-width: 620px) {
  .header__info {
    display: none;
    position: absolute;
  }
}
.header__info a {
  display: block;
}
.header__info_phone {
  color: var(--color-black-normal);
  font-size: 15px;
  font-weight: 500;
  line-height: 18px;
}
.header__info_link {
  margin-top: 6px;
  color: var(--color-grey-light);
  font-size: 13px;
  font-weight: 400;
  line-height: 120%;
}
@media (max-width: 925px) {
  .header__btn {
    display: none;
  }
}

.btn {
  width: 190px;
  height: 52px;
  background-color: var(--color-red-norml);
  border: none;
  border-radius: 14px;
  cursor: pointer;
  color: var(--color-white-normal);
  font-size: 14px;
  font-weight: 400;
  line-height: 14px;
}
@media (hover: hover) {
  .btn:hover:hover {
    background-color: var(--color-red-hover);
  }
  .btn:hover:active {
    background-color: var(--color-red-active);
  }
}
@media (hover: none) {
  .btn:active:hover {
    background-color: var(--color-red-hover);
  }
  .btn:active:active {
    background-color: var(--color-red-active);
  }
}

.pages {
  width: 100%;
  height: 56px;
  background-color: var(--color--grey-normal);
  border-bottom: 2px solid var(--color-grey-light);
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (max-width: 768px) {
  .pages {
    height: 48px;
  }
}
.pages__list {
  min-width: 131px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.pages__list-item_link {
  color: var(--color-grey-superlight);
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
}
.pages__list-item_link_active {
  color: var(--color-grey-light);
}

.title_wrapper {
  width: 100%;
  height: 100px;
  background-color: var(--color--grey-normal);
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 768px) {
  .title_wrapper {
    height: 62px;
  }
}

.title {
  font-family: "RoadRadio" sans-serif;
  color: var(--color-black-normal);
  font-size: clamp(22px, calc(46 / 12.9)vw, 46px);
  font-weight: 400;
  line-height: 100%;
}

.contacts__wrapper {
  width: 100%;
  height: auto;
}

.contacts {
  width: 100%;
  height: auto;
  padding: 62px 0 66px 0;
  background-color: var(--color-white-normal);
}
@media (max-width: 768px) {
  .contacts {
    padding: 40px 0 50px 0;
  }
}
@media (max-width: 320px) {
  .contacts {
    padding: 30px 0 50px 0;
  }
}
.contacts__inner {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
@media (max-width: 700px) {
  .contacts__inner {
    flex-wrap: wrap;
    justify-content: center;
  }
}
.contacts__info {
  width: clamp(267px, calc(607 / 12.9)vw, 607px);
}
@media (max-width: 700px) {
  .contacts__info {
    width: 100%;
  }
}
.contacts__info ul {
  width: clamp(267px, calc(506 / 12.9)vw, 506px);
  margin-top: 20px;
  font-family: "Golos Text", sans-serif;
  color: var(--color-grey-superlight);
  font-size: clamp(13px, calc(13 / 12.9)vw, 13px);
  font-weight: 400;
  line-height: 150%;
}
@media (max-width: 700px) {
  .contacts__info ul {
    width: 100%;
  }
}
.contacts__info ul:nth-of-type(1) {
  margin-bottom: 15px;
  color: var(--color-black-normal);
  font-size: clamp(14px, calc(17 / 12.9)vw, 17px);
  font-weight: 400;
  line-height: 110%;
}
.contacts__info ul:nth-of-type(1) li {
  margin-top: 6px;
}
.contacts__info-links a {
  display: block;
  width: max-content;
  font-family: "RoadRadio" sans-serif;
  font-size: clamp(28px, calc(36 / 12.9)vw, 36px);
  color: var(--color-black-normal);
  font-weight: 400;
  line-height: 110%;
}
.contacts__info-links a:nth-child(2) {
  margin-top: 6px;
  margin-bottom: 20px;
  font-family: "Golos Text", sans-serif;
  color: var(--color-red-norml);
  font-size: clamp(25px, calc(25 / 12.9)vw, 25px);
  font-weight: 400;
  line-height: 30px;
}
.contacts__form {
  /* @include w.fluid-width(290, 630); */
}
@media (max-width: 1287px) {
  .contacts__form {
    max-width: 350px;
    display: flex;
    flex-direction: column;
  }
}
@media (max-width: 700px) {
  .contacts__form {
    margin-top: 50px;
  }
}
.contacts__form-title {
  margin-bottom: 20px;
  font-family: "RoadRadio" sans-serif;
  font-size: clamp(20px, calc(36 / 12.9)vw, 36px);
  color: var(--color-black-normal);
  font-weight: 400;
  line-height: 110%;
}
.contacts .form {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 28px;
}
.contacts .form__row-block {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  row-gap: 28px;
}
.contacts .form__inputs {
  padding: 17px 17px 17px 20px;
  border-radius: 14px;
  background-color: var(--color--grey-normal);
  border: none;
  outline: none;
  /* border: 4px solid var(--resetcolor); */
  color: var(--color-black-normal);
  font-family: "Golos Text", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 100%;
}
.contacts .form__inputs:-webkit-autofill {
  color: var(--color-red-norml) !important;
}
.contacts .form__inputs:-internal-autofill-selected {
  border: none;
  appearance: none;
  background-image: none !important;
  background-color: var(--resetcolor) !important;
  box-shadow: 0 0 0px 1000px var(--resetcolor) inset;
}
.contacts .form__inputs::placeholder {
  color: var(--color-grey-light);
  font-family: "Golos Text", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
}
.contacts .form__inputs:focus {
  border: 2px solid var(--color-grey-light);
  background-color: var(--color-white-normal);
}
.contacts .form__inputs[type=text] {
  width: 300px;
  height: 52px;
  min-width: 290px;
}
@media (max-width: 1287px) {
  .contacts .form__inputs[type=text] {
    width: 350px;
    margin-right: 0 !important;
  }
}
@media (max-width: 400px) {
  .contacts .form__inputs[type=text] {
    width: 100%;
  }
}
.contacts .form__inputs[name=textarea] {
  width: 100%;
  height: 133px;
  min-width: 290px;
  resize: none;
}
@media (max-width: 1287px) {
  .contacts .form__inputs[name=textarea] {
    max-width: 350px;
    height: 166px;
  }
}
.contacts .form__inputs[type=checkbox] {
  width: 100%;
  height: 133px;
  min-width: 290px;
}
.contacts .form__checkbox {
  width: 100%;
  display: flex;
  gap: 16px;
}
.contacts .form__checkbox label {
  color: var(--color-grey-light);
  font-family: "Golos Text", sans-serif;
  font-size: 13px;
  font-weight: 400;
  line-height: 120%;
  cursor: pointer;
}
.contacts .form__checkbox label span {
  color: var(--color-black-normal);
}
.contacts .form__checkbox-wrap {
  position: relative;
  width: 20px;
  height: 20px;
  flex-shrink: 0;
}
.contacts .form__checkbox input {
  position: absolute;
  width: 100%;
  height: 100%;
  scale: 0.8;
  cursor: pointer;
  z-index: 9;
}
.contacts .form__checkbox input:checked + .form__checkbox-fake {
  background-color: var(--color-red-norml);
}
.contacts .form__checkbox input:checked + .form__checkbox-fake img {
  display: block;
}
@media (hover: hover) {
  .contacts .form__checkbox input:hover:hover + .form__checkbox-fake {
    background-color: var(--color-red-norml);
  }
  .contacts .form__checkbox input:hover:active + .form__checkbox-fake {
    background-color: var(--color-red-active);
  }
}
@media (hover: none) {
  .contacts .form__checkbox input:active:hover + .form__checkbox-fake {
    background-color: var(--color-red-norml);
  }
  .contacts .form__checkbox input:active:active + .form__checkbox-fake {
    background-color: var(--color-red-active);
  }
}
.contacts .form__checkbox-fake {
  position: absolute;
  z-index: 100;
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  background-color: var(--color--grey-normal);
  pointer-events: none;
}
.contacts .form__checkbox-fake img {
  display: none;
}
.contacts .form__btn {
  display: block;
  align-self: stretch;
}

.invalid {
  display: inline-block;
  height: max-content;
  position: relative;
}
.invalid input,
.invalid textarea {
  position: relative;
  background-color: var(--color-red-error-bg) !important;
  border: none !important;
}
.invalid input::placeholder,
.invalid textarea::placeholder {
  color: var(--color-red-norml) !important;
}
.invalid:after {
  content: "Является обязательным полем";
  width: max-content;
  height: 10px;
  position: absolute;
  bottom: -10px;
  left: 20px;
  color: var(--color-red-norml);
  font-family: "Golos Text", sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 150%;
}

.cards {
  width: 100%;
  height: auto;
  padding: 81px 0 100px 0;
  background-color: var(--color-white-normal);
}
@media (max-width: 768px) {
  .cards {
    padding: 59px 0 80px 0;
  }
}
@media (max-width: 320px) {
  .cards {
    padding: 30px 0 60px 0;
  }
}
.cards__wrapper {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, 300px);
  grid-auto-rows: minmax(310px, max-content);
  gap: 30px;
  justify-content: center;
  transition: 0.2s;
}
@media (max-width: 1338px) {
  .cards__wrapper {
    gap: 10px;
    grid-template-columns: repeat(auto-fit, 350px);
  }
}
@media (max-width: 758px) {
  .cards__wrapper {
    grid-template-columns: repeat(auto-fit, 288px);
  }
}
@media (max-width: 635px) {
  .cards__wrapper {
    grid-template-columns: repeat(1, 288px);
  }
}
.cards__item {
  width: 100%;
  height: auto;
  border-radius: 20px;
  background-color: var(--color--grey-normal);
  padding: 33px 19px 40px 19px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (max-width: 768px) {
  .cards__item {
    padding: 33px 15px 40px 15px;
  }
}
.cards__item:last-child {
  grid-column: 3/5;
}
@media (max-width: 1338px) {
  .cards__item:last-child {
    grid-column: 1/4;
  }
}
@media (max-width: 1102px) {
  .cards__item:last-child {
    grid-column: 1/3;
  }
}
@media (max-width: 635px) {
  .cards__item:last-child {
    grid-column: 1/2;
  }
}
.cards__item-city {
  text-transform: uppercase;
  margin-bottom: 15px;
  color: var(--color-black-normal);
  font-family: "RoadRadio" sans-serif;
  font-style: normal;
  font-size: 26px;
  font-weight: 400;
  line-height: 110%;
}
.cards__item-phone {
  display: inline-block;
  color: var(--color-black-normal);
  font-family: "Golos Text", sans-serif;
  font-style: normal;
  font-size: 19px;
  font-weight: 400;
  line-height: 150%;
}
.cards__item-phone:nth-child(1) {
  margin-bottom: 0;
}
.cards__item-address {
  margin-top: 10px;
  color: var(--color-grey-light);
  font-family: "Golos Text", sans-serif;
  font-style: normal;
  font-size: 14px;
  font-weight: 400;
  line-height: 140%;
}
.cards__item-route {
  display: block;
  color: var(--color-red-norml);
  font-family: "Golos Text", sans-serif;
  font-style: normal;
  font-size: 14px;
  font-weight: 400;
  line-height: 140%;
}
.cards__item-worktime {
  color: var(--color-black-normal);
  font-family: "Golos Text", sans-serif;
  font-style: normal;
  font-size: 14px;
  font-weight: 400;
  line-height: 140%;
}

@media (max-width: 1060px) {
  :root {
    --padding: 87px;
  }
}
@media (max-width: 768px) {
  :root {
    --padding: 40px;
  }
}
footer {
  width: 100%;
  position: relative;
  background: linear-gradient(90deg, var(--color-black-dark) 50%, var(--color-grey-dark) 50%) no-repeat center/cover;
}
@media (max-width: 700px) {
  footer {
    background: linear-gradient(180deg, var(--color-black-dark) 45%, var(--color-grey-dark) 45%) no-repeat center/cover;
  }
}

.footer {
  width: 100%;
  min-height: 467px;
  display: flex;
  align-items: stretch;
  font-family: "Golos Text", sans-serif;
  padding: 63px 0 111px 0;
}
@media (max-width: 700px) {
  .footer {
    flex-direction: column;
    padding: 40px 0 134px 0;
    gap: 79px;
    align-items: center;
  }
}
.footer__menu-wrapper {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-right: var(--padding);
}
@media (max-width: 700px) {
  .footer__menu-wrapper {
    width: 100%;
    padding-right: 0;
  }
}
.footer__contacts-wrapper {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-left: var(--padding);
}
@media (max-width: 700px) {
  .footer__contacts-wrapper {
    width: 100%;
    padding-left: 0;
  }
}
.footer__list {
  display: flex;
  flex-direction: column;
  gap: 15px;
}
@media (max-width: 768px) {
  .footer__list {
    gap: 10px;
  }
}
.footer__list_item-link {
  color: rgb(255, 255, 255);
  font-family: inherit;
  font-size: clamp(20px, calc(24 / 12.9)vw, 24px);
  font-weight: 400;
  line-height: 140%;
}
.footer__menu {
  max-width: 488px;
  display: flex;
  flex-wrap: wrap;
  column-gap: 10px;
  row-gap: 15px;
}
@media (max-width: 768px) {
  .footer__menu {
    gap: 10px;
  }
}
.footer__menu-item_link {
  font-family: inherit;
  font-size: 16px;
  font-weight: 400;
  line-height: 180%;
  color: var(--color-grey-light);
}
.footer__menu-item_link:hover {
  color: var(--color-white-normal);
}
.footer__contacts {
  display: flex;
  column-gap: 61px;
  row-gap: 20px;
  flex-wrap: wrap;
  align-items: stretch;
}
.footer__contacts-phone {
  display: block;
  color: var(--color-white-normal);
  font-family: inherit;
  font-size: clamp(23px, calc(28 / 12.9)vw, 28px);
  font-weight: 500;
  line-height: 130%;
}
.footer__contacts_btn {
  background-color: transparent;
  border: 2px solid rgb(73, 73, 80);
}
.footer__contacts-link {
  color: rgb(242, 49, 71);
  font-family: inherit;
  font-size: clamp(20px, calc(24 / 12.9)vw, 24px);
  font-weight: 400;
  line-height: 140%;
}
@media (max-width: 700px) {
  .footer__contacts-link {
    margin-bottom: 42px;
  }
}
.footer__contacts-address {
  max-width: 447px;
  color: var(--color-white-normal);
  font-family: inherit;
  font-size: 16px;
  font-weight: 400;
  line-height: 130%;
}

.subfooter {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 68px;
  border-top: 2px solid var(--color-grey-light);
  background-color: transparent;
}
@media (max-width: 520px) {
  .subfooter {
    height: 85px;
  }
}
.subfooter .container {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 520px) {
  .subfooter .container {
    flex-direction: column;
    justify-content: center;
    align-items: start;
    gap: 10px;
  }
}
.subfooter p {
  width: 50%;
  padding-right: var(--padding);
}
.subfooter p:nth-child(2) {
  padding-right: 0;
  padding-left: var(--padding);
}
@media (max-width: 520px) {
  .subfooter p {
    width: 100%;
    padding-left: 0 !important;
    padding-right: 0 !important;
  }
}
.subfooter__monevak a {
  color: var(--color-grey-light);
  font-family: "Golos Text", sans-serif;
  font-size: 13px;
  font-weight: 400;
  line-height: 110%;
}
.subfooter__todo span {
  color: var(--color-grey-light);
  font-family: "Golos Text", sans-serif;
  font-size: 13px;
  font-weight: 400;
  line-height: 110%;
}
.subfooter__todo span a {
  color: rgb(201, 201, 207);
}